<template>
  <!-- cintainer布局 -->
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div class="">
        <img src="../assets/img/heima.png" alt="" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <!-- 内容主体区 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside :width="isCollapse ? '64px' :'200px'">
          <div class="toggle_button" @click="isCollapse = !isCollapse">|||</div>
        <el-menu background-color="#333744" text-color="#fff" active-text-color="#409eff" unique-opened :collapse="Boolean(isCollapse)" :collapse-transition="false" :router="true" >
          <el-submenu :index=" String( item.id)" v-for="item in menulist" :key="item.id" >
              <!-- 一级菜单 -->
            <template slot="title">
              <i :class="iconfontObj[item.id]"></i>
              <span>{{item.authName}}</span>
            </template>
            <!-- 二级菜单项 -->
            <el-menu-item :index=" '/'+ subItme.path "  v-for="subItme of item.children" :key="subItme.id" :default-openeds="activePath" @click="saveNavStatu('/'+ item.path)">
                <i class="el-icon-menu"></i>
               <span>{{subItme.authName}}</span>
            </el-menu-item>
           
          </el-submenu>

        </el-menu>
      </el-aside>
      <!-- 内容区 -->
      <el-main>
        <!-- 路由占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
export default {
    data() {
        return {
            // 菜单数据
            menulist:[],
            iconfontObj:{
                '125':'iconfont icon-user',
                '103':'iconfont icon-tijikongjian',
                '101':'iconfont icon-shangpin',
                '102':'iconfont icon-danju',
                '145':'iconfont icon-baobiao',

            },
            isCollapse:false,
            // 状态栏激活的地址
            activePath:''

        }
    },
    created(){
        this.getMessageList()
        this.activePath = window.sessionStorage.getItem('activePath')
    },
    methods: {
        logout() {
        window.sessionStorage.clear()
        this.$router.push('/login')
        },
        // 获取菜单栏数据
        async getMessageList(){
            const {data:res} = await this.$http.get('menus')
            if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
            this.menulist = res.data
            // console.log(res)
        },
        saveNavStatu(activePath){
            window.sessionStorage.setItem("activePath",activePath)
            this.activePath = activePath
        },
        
    }
}
</script>
<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}

.el-aside {
  background-color: #333744;
  .el-menu {
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}

.iconfont {
  margin-right: 10px;
}

.toggle_button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
