<template>
  <div class="login_container">
    <div class="login_box">
        <!-- 头像区 -->
        <div class="avatar_box">
            <img src="../assets/logo.png" alt="">
        </div>
        <!-- 登录区 -->
        <el-form ref="loginFormRef" :model="loginForm" class='login_form' :rules="loginFormRules">
            <el-form-item prop="username"> 
                <!-- 用户名 -->
                <el-input  
                    prefix-icon="iconfont icon-user"
                    v-model="loginForm.username"
                    placeholder="请输入用户名"
                >
                </el-input>
            </el-form-item>
            <el-form-item prop="password">
                <!-- 密码 -->
                <el-input
                    prefix-icon="iconfont icon-3702mima"
                    v-model="loginForm.password"
                    placeholder="请输入密码"
                    show-password
                >
                </el-input>
            </el-form-item>
            <!-- 按钮区 -->
            <el-form-item label="" class="btns">
                <el-button type='primary' @click="login">登录</el-button>
                <el-button type='info' @click="restloginForm">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
  </div>
</template>
<script>
export default {
    data() {
        return {
            loginForm:{
                username:'admin',
                password:'123456'
            },
            // 表单的认证规则对象
            loginFormRules: {
                // 验证用户名
                    username: [
                        { required: true, message: '请输入登录的账号', trigger: 'blur' },
                        { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                    ],
                // 验证密码
                    password: [
                        { required: true, message: '请输入登录密码', trigger: 'blur' },
                        { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
                    ],
             }
        }
    },
    methods:{
        // 点击重置按钮，丛植登录表单
       restloginForm(){
        //    console.log(this)
        this.$refs.loginFormRef.resetFields()
       } ,
       login(){
        //    验证表单
        this.$refs.loginFormRef.validate(async validate => {
                // console.log(validate)
                if(!validate) return;
                const {data: res} =await this.$http.post('login',this.loginForm);
                if(res.meta.status !== 200) return this.$message.error("登录失败！账号或密码错误");
                this.$message.success("登录成功");
                // 1. 将登陆成功后的 token，保存到客户端的 sessionSting 里
                    // 1. 1 项目中出现登陆之外的其它API接口，必须在登录之后才能访问
                    // 2. 2 token 只应在当前的网站打开期间生效，所以将 token 保存在 sessionSting 中
                // 2. 通过编程式导航跳转到后台主页，路由地址是 /home
                window.sessionStorage.setItem('token',res.data.token)
                //将token保存到sessionSting里
                this.$router.push('/home')
                // 编程式导航
           })
           
       }
    }
};
</script>
